<template>
  <div class="el-result">
    <div class="el-result__icon" :style="imageStyle">
      <slot name="icon">
        <component :is="iconElement" :class="iconElement" />
      </slot>
    </div>
    <div v-if="title || $slots.title" class="el-result__title">
      <slot name="title">
        <p>{{ title }}</p>
      </slot>
    </div>
    <div v-if="subTitle || $slots.subTitle" class="el-result__subtitle">
      <slot name="subTitle">
        <p>{{ subTitle }}</p>
      </slot>
    </div>
    <div v-if="$slots.extra" class="el-result__extra">
      <slot name="extra"></slot>
    </div>
  </div>
</template>
<script>
import IconSuccess from './icon-success.vue';
import IconError from './icon-error.vue';
import IconWarning from './icon-warning.vue';
import IconInfo from './icon-info.vue';

import IconLoading from './icon-loading.vue';
import IconSubmit from './icon-submit.vue';

import IconEmpty1 from './icon-empty1.vue';
import IconEmpty2 from './icon-empty2.vue';
import IconEmpty3 from './icon-empty3.vue';

import IconTime1 from './icon-time1.vue';
import IconTime2 from './icon-time2.vue';
import IconTime3 from './icon-time3.vue';

import IconWait1 from './icon-wait1.vue';
const IconMap = {
  success: 'icon-success',
  warning: 'icon-warning',
  error: 'icon-error',
  info: 'icon-info',
  loading: 'icon-loading',
  submit: 'icon-submit',
  empty1: 'icon-empty1',
  empty2: 'icon-empty2',
  empty3: 'icon-empty3',
  time1: 'icon-time1',
  time2: 'icon-time2',
  time3: 'icon-time3',
  wait1: 'icon-wait1'
};
export default {
  name: 'ElResult',
  components: {
    IconSuccess,
    IconError,
    IconWarning,
    IconInfo,
    IconLoading,
    IconEmpty1,
    IconEmpty2,
    IconEmpty3,
    IconTime1,
    IconTime2,
    IconTime3,
    IconSubmit,
    IconWait1
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    subTitle: {
      type: String,
      default: ''
    },
    icon: {
      type: String,
      default: 'info'
    },
    size: {
      type: Number,
      default: 64
    }
  },
  computed: {
    iconElement() {
      const icon = this.icon;
      return icon && IconMap[icon] ? IconMap[icon] : 'icon-info';
    },
    imageStyle() {
      return { width: this.size ? `${this.size}px` : '' };
    }
  }
};
</script>
